.backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(247, 177, 247, .094);
  transition: opacity .25s, visibility .25s;
  position: fixed;
  top: 0;
}

.modal {
  width: 450px;
  background-color: #fff5f6;
  border-radius: 15px;
  padding: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: rotate(360deg)translate(-50%, -50%)scale(1);
  box-shadow: 0 8px 30px rgba(212, 20, 67, .1);
}

@media (min-width: 320px) and (max-width: 450px) {
  .modal {
    width: 100%;
  }
}

@media screen and (min-width: 1280px) {
  .modal {
    width: 528px;
  }
}

@media screen and (max-width: 1279px) {
  .modal {
    max-height: 100%;
    overflow-y: auto;
  }
}

.modal-close {
  width: 32px;
  height: 32px;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #9b9191;
  border-radius: 50%;
  line-height: 8px;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 10px;
  right: 10px;
}

.backdrop-close-icon {
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.backdrop-close-icon:hover, .backdrop-close-icon:focus {
  fill: #ffa5ba;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.backdrop__title {
  text-align: center;
  letter-spacing: .03em;
  color: #000;
  margin-bottom: 12px;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.15;
}

.modal-form__field {
  margin-bottom: 10px;
}

.modal-form__label {
  letter-spacing: .01em;
  color: #000;
  margin-bottom: 4px;
  font-size: 12px;
  line-height: 1.16;
  display: block;
}

.modal-form__wrap {
  position: relative;
}

.modal-form__input {
  width: 100%;
  height: 40px;
  cursor: pointer;
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  outline: none;
  margin-bottom: 10px;
  padding-left: 15px;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.modal-form__input:hover, .modal-form__input:focus {
  border-color: #ffa5ba;
}

.modal-form__text {
  width: 100%;
  height: 120px;
  resize: none;
  border: 1px solid rgba(39, 37, 39, .2);
  border-radius: 4px;
  outline: none;
  padding-top: 12px;
  padding-left: 16px;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1);
}

.modal-form__text:hover, .modal-form__text:focus {
  border-color: #ffa5ba;
}

.modal-form__note {
  letter-spacing: .03em;
  color: #000;
  font-size: 14px;
  line-height: 1.7;
}

@media screen and (max-width: 1199px) {
  .modal-form__note {
    font-size: 12px;
    line-height: 1.6;
  }
}

.modal-form__note span {
  width: 16px;
  height: 15px;
  border: 2px solid #212121;
  border-radius: 2px;
  justify-content: center;
  align-items: center;
  margin-right: 8px;
  display: flex;
}

.modal-form__check:checked + .modal-form__note span {
  background-color: #fab4ff;
  border: none;
}

.modal-form__link {
  color: #1f71ff;
  text-decoration: underline;
}

.modal-form__button {
  justify-content: center;
  margin-top: 30px;
  display: flex;
}

.modal-form__send {
  min-width: 200px;
  height: 50px;
  color: #fff;
  letter-spacing: .06em;
  background: #d41342;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 22px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.9;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1);
}

.modal-form__send:hover, .modal-form__send:focus {
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

