.products {
  padding-top: 80px;
  padding-bottom: 40px;
  @media screen and (min-width: 768px) {
    padding-top: 40px;
    padding-bottom: 60px;
  }
  @media screen and (min-width: 1280px) {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}
.products__title {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 148px;
  font-family: 'Titan One';
  font-weight: 400;
  font-size: 34px;
  line-height: 1.14;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $red;

  @include mq(tablet){ 
    font-size: 48px;
    margin-bottom: 154px;
}

@include mq(desktop){ 
    font-size: 58px;
    margin-bottom: 261px;
}


}

.products__label {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 15px;
  font-family: 'Titan One';
  font-weight: 400;
  font-size: 18px;
  line-height: 1.16;
  text-align: center;
  text-transform: uppercase;
  color: $pink;
  
  @include mq(tablet){ 
    font-size: 26px;
    line-height: 1.53;
}

@include mq(desktop){ 
    font-size: 30px;
    line-height: 1.13;
}
  
}

.products__list{
    @include mq(tablet){
        display: flex;
        justify-content: center;
        align-items: center;
       
    }

    
}

.products__item {
  margin-bottom: 140px;
  &:last-child {
    margin-bottom: 0;
  }

  @include mq(tablet){
    margin-right: 16px;
    margin-bottom: 0;
    &:last-child{
        margin-right: 0;
    }
}
@include mq(desktop){ 
    margin-right: 32px;
}
}

.products__wrapper {
  position: relative;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 280px;
  border-radius: 24px;
  padding-top: 160px;
  padding-bottom: 40px;

  @include mq(tablet){ 
    width: 224px;
    padding-top: 119px;
    
}
@include mq(desktop){ 
    width: 395px;
    padding-top: 165px;
    padding-bottom: 60px;
}
}

.products__wrapper--fist {
  background-color: $pinkBackG;
}

.products__wrapper--second {
  background-color: $brownBackG;
}

.products__wrapper--third {
  background-color: $greenBackG;
}

.products__wrapper::before {
  content: '';
  display: block;
  margin-left: auto;
  margin-right: auto;
  position: absolute;
  top: -140px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 280px;
  height: 280px;

  @include mq(tablet){ 
    width: 224px;
    height: 224px;
    }
    @include mq(desktop){ 
    width: 394px;
    height: 394px;
    top: -255px;
    }

}

.products__wrapper--fist::before {
  content: '';
  
  background-image: url(../images/icecream-mobile.png);
    @include mq(retina){
        background-image: url(../images/icecream-mobile@x2.png);
    }

    @include mq(tablet){ 
        background-image: url(../images/icecream-tablet.png);
        @include mq(retina){
            background-image: url(../images/icecream-tablet@x2.png);
        }
        
    }

    @include mq(desktop){ 
        background-image: url(../images/icecream-desktop.png);
        @include mq(retina){
            background-image: url(../images/icecream-desktop@x2.png);
        }
        
    }
    
}

.products__wrapper--second::before {
  content: '';
  background-image: url(../images/icecoffee-mobile.png);
  @include mq(retina){
    background-image: url(../images/icecoffee-mobile@x2.png);
    }
    @include mq(tablet){ 
    background-image: url(../images/icecoffee-tablet.png);
    @include mq(retina){
        background-image: url(../images/icecoffee-tablet@x2.png);
    }
    
    }
    @include mq(desktop){ 
    background-image: url(../images/icecoffee-desktop.png);
    @include mq(retina){
        background-image: url(../images/icecoffee-desktop@x2.png);
    }
    
}
 
}

.products__wrapper--third::before {
    content: '';
    background-image: url(../images/milkshake-mobile.png);
    @include mq(retina){
      background-image: url(../images/milkshake-mobile@x2.png);
    }
    
    @include mq(tablet){ 
        background-image: url(../images/milkshake-tablet.png);
        @include mq(retina){
            background-image: url(../images/milkshake-tablet@x2.png);
        }
    }
    
     @include mq(desktop){ 
         background-image: url(../images/milkshake-desktop.png);
        @include mq(retina){
            background-image: url(../images/milkshake-desktop@x2.png);
            }
    }

}

.products__subtitle {
  position: relative;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 90px;
  width: 174px;
  height: 23px;
  font-family: 'Titan One';
  font-weight: 400;
  font-size: 20px;
  line-height: 1.15;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $white;

}

.products__subtitle::after {
  content: '';
  background-image: url('../images/icon/dots_group.svg');
  
  display: block;
  width: 26px;
  height: 6px;
  margin-right: auto;
  margin-left: auto;
  margin-top: 32px;

}

.products__about {
  width: 200px; 
  margin-bottom: 32px;
  margin-left: auto;
  margin-right: auto;
  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.5;
  text-align: center;
  letter-spacing: 0.04em;
  color: $white;

  @include mq(tablet){ 
    width: 160px;
    }

  @include mq(desktop){ 
        width: 265px;
        margin-bottom: 60px;
    }

}

#more{
    display: none;
}



.products__description {
  width: 40px;
  height: 40px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  border: transparent;
  border-radius: 50%;
  cursor: pointer;
  background-color: $whiteBtnHoverBackG;
  color: $redBtnHoverBackG;
   
    
  transition-property: background-color, transform;
  transition-duration: 300ms;
  transition-timing-function: linear;
  transition-delay: 0;


    &:hover,
    &:focus{
        background-color: $greyBackG;
        transform: scale(1.2);
    }
    
}

.products__btn-icon {
  display: block;
  margin-right: auto;
  margin-left: auto;
  align-items: center;
  width: 12px;
  height: 10px;
  fill: currentColor;
}
